<template lang="html">

  <h1>Baza Korisnika i Kompanija</h1>

  <TabNav :tabs="companyList" :selected="selected"  @selected="setSelected" id="companyNav">
    <Tab v-for="(company,i) in companyList" :key="company" :isSelected="selected === company ">

      <div class="validation">
        {{validation}}
      </div>
      <div class="container" v-for="userX in listOfListsOfUsers[i]"  :key="userX._id" >
        <table>
          <tr>
            <th>ime</th>
            <th>email</th>
            <th>username</th>
            <th>sektor</th>
            <th>radno mesto</th>
          </tr>
          <tr>
            <td>{{userX.name}}</td>
            <td>{{userX.email}}</td>
            <td>{{userX.username}}</td>
            <td>{{userX.sektor}}</td>
            <td>{{userX.radnoMesto}}</td>
          </tr>

        </table>
        <button type="button" name="button" @click="removeUser(userX._id)">Ukloni Zaposlenog</button>
      </div>
    </Tab>
  </TabNav>

</template>

<script>
// @ is an alias to /src
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
// eslint-disable-next-line no-unused-vars
import TabNav from '@/components/TabNav.vue'
import Tab from '@/components/Tab.vue'
export default {
  computed: mapGetters(['companys',"users","user"]),
  components:{
    TabNav,
    Tab,
  },
  data(){
    return{
      selected: '',
      companyList:[],
      //each element is a list of users (different lists are for different companies)
      listOfListsOfUsers:[],
      validation:"",
    }
  },
  methods: {
    ...mapActions(['getCompanies','getAllUsers','getProfile','deleteUser']),
    setSelected(tab){
      this.selected=tab;
    },
    removeUser(userID){
      this.validation="";
      this.deleteUser(userID);
      this.validation=`Uklonjen Zaposleni id=${userID}`;
    }
  },
  async created() {
    await this.getProfile();
    await  this.getCompanies();
    await this.getAllUsers();

    //Fill list of companies (companyList is used by Tabs)
    this.companys.forEach(company => {
      let list = [];
      this.listOfListsOfUsers.push(list);
      if(company.name!="admin"){
      this.companyList.push(company.name);
      }
    });
    for (let i = 0; i < this.listOfListsOfUsers.length; i++) {
      let companyName = this.companyList[i];
      if(companyName!="admin"){
      for(let j = 0; j< this.users.length; j++){
        if(this.users[j].type === companyName){
          this.listOfListsOfUsers[i].push(this.users[j]);
        }
      }
    }
    }
  },
}
</script>

<style lang="css" scoped>

table{
  border: 2px solid black;
  text-align: center;
}
td, th{
  text-align: center;
  border: 1px solid black;
}
tr:nth-child(even){background-color: #aaaacc;}
tr:nth-child(odd){background-color: #ccccff;}
tr:hover {background-color: #afa;}

.container{
  margin-top: 10px;
}

.validation{
  margin-top: 5px;
  background-color: #00aaaa;
}

</style>
